import React, { useState, FormEvent } from 'react';
import axios from 'axios';
import ReCAPTCHA from "react-google-recaptcha"

import './styles.css';

function Contact() {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [body, setBody] = useState('');
    const [captcha, setCaptchaVal] = useState('');
    const [inUse, setInUse] = useState(false);
    
    function onChange(value: any) {
        setCaptchaVal(value);
    }
    
    function sendMail(e: FormEvent) {
        if(inUse)
            return;

        setInUse(true);
        e.preventDefault();

        axios.post('https://murilopereira-mailer.herokuapp.com/mail', {
            name,
            email,
            body,
            captcha
        }).then(() => {
            alert('Mensagem enviada com sucesso!');            
        }).catch((error) => {
            switch(error.response.status) {
                case 500:
                    alert('Oops... Houve um erro. Tente novamente em instantes');
                    break;
                case 400:
                    alert(error.response.status.message);
                    break;
                default:
                    alert('Erro no envio!');
                    break;
            }
        });

        setName('');
        setEmail('');
        setBody('');
        setCaptchaVal('');
        setInUse(false);
    }

    return (
        <div className="contactContainer"> 
            <div className="contactHeader">
                <p>C O N T A T O</p>
            </div>
            <div className="contactForm">
                <form action="" id="sendMessage">
                    <label htmlFor="name">Qual seu nome?</label>
                    <input 
                        id="name" 
                        placeholder="Nome" 
                        type="text"
                        value={name} 
                        onChange={(e) => { setName(e.target.value) }} 
                    />
                    <label htmlFor="mail">Qual seu email?</label>
                    <input 
                        id="mail" 
                        placeholder="Email" 
                        type="mail"
                        value={email} 
                        onChange={(e) => { setEmail(e.target.value) }}    
                    />
                    <label htmlFor="text">Fale sobre sua ideia</label>
                    <textarea 
                        maxLength={300} 
                        placeholder="Diga o que quiser em 300 caracteres" 
                        id="text"
                        value={body} 
                        onChange={(e) => { setBody(e.target.value) }}
                    >

                    </textarea>
                    <div className="contactFooter">
                        <ReCAPTCHA theme="dark" sitekey="6Ldf_r0ZAAAAACZ9GbErSax88V6jiKyDYhNG_UsK" onChange={onChange} />
                        <button onClick={sendMail} type="submit">ENVIAR</button>
                    </div>
                </form>
            </div>
        </div>
    );
}

export default Contact;