import React from 'react';
import './styles.css';

interface projectProps {
    src: string,
    title?: string,
    href?: string,
    tech?: string
}

const Projects: React.FC<projectProps> = (props) => {
    return (
        <div className="projectItem">            
                <a href={props.href} target="_blank" className="anchorProject">
                    <img className="image" width="100%" src={props.src} />
                </a>
                <div className="overlay">
                    <div className="overlayWrapper">
                        <div className="text">{props.title}</div>
                        <div className="techs">{props.tech}</div>
                        <button onClick={()=> window.open(props.href, "_blank")}>Saiba mais</button>
                    </div>                
                </div>
        </div>
    );
}

export default Projects;